import { Glue42Core } from "@glue42/core";
import callbackRegistry, { CallbackRegistry, UnsubscribeFunction } from "callback-registry";
import { Glue42 } from "../../glue";
import groupFactory from "./group";
import { default as windowStore } from "./store";
import {
    Environment,
    GroupInternal,
    GroupsInternal,
    GroupsWrapper,
    GroupWrapper,
    WindowsLibrary,
    WindowWrapper
} from "./types";

export default (environment: Environment, logger: Glue42Core.Logger.API): GroupsWrapper => {
    const _logger = logger.subLogger("groups");
    const _registry: CallbackRegistry = callbackRegistry();
    const _groups: { [key: string]: GroupWrapper } = {};
    let heardForWindowsCounter: number = -1;

    // Getting already existing windows and create Group API for each window
    const windows = windowStore.list;
    Object.keys(windows).forEach((k) => {
        addNewWindow(windows[k]);
    });

    environment.onCompositionChanged((windowInfo: any) => {
        handleCompositionChanged(windowInfo);
    });

    environment.onGroupHeaderVisibilityChanged((windowInfo) => {
        const windowId = windowInfo.windowId;
        const group: Glue42.Windows.Group = findGroupByWindow(windowId);
        if (typeof group !== "undefined") {
            const groupEvents: GroupWrapper = _groups[group.id];

            if (heardForWindowsCounter === -1) {
                heardForWindowsCounter = group.windows.length;
            }

            heardForWindowsCounter--;
            if (heardForWindowsCounter === 0) {
                heardForWindowsCounter = -1;
                groupEvents.groupInternal.handleGroupHeaderVisibilityChanged(windowInfo);
            }
        }
    });

    windowStore.onAdded((win) => {
        addNewWindow(win);
    });
    windowStore.onRemoved((win) => {
        removeWindow(win);
    });

    // Methods

    function my(): Glue42.Windows.Group {
        return findGroupByWindow(environment.my());
    }

    function list(success?: (groups: Glue42.Windows.Group[]) => void): Glue42.Windows.Group[] {
        const result: Glue42.Windows.Group[] = Object.keys(_groups).map((groupId) => {
            if (_groups[groupId]) {
                return _groups[groupId].groupAPI;
            }

        });

        if (typeof success === "function") {
            success(result);
        }
        return result;
    }

    function findGroupByWindow(winId: string | Glue42.Windows.GDWindow, success?: (group: Glue42.Windows.Group) => void): Glue42.Windows.Group {
        let windowId: string;
        if (typeof winId === "undefined") {
            logger.debug("trying to find a group by a window, but winId is undefined");
            return;
        }
        if (typeof winId === "string") {
            windowId = winId;
        } else {
            windowId = winId.id;
        }
        let groupFound: Glue42.Windows.Group;
        Object.keys(_groups).forEach((groupId: string) => {
            const group: Glue42.Windows.Group = _groups[groupId].groupAPI;
            if (group.find(windowId) !== undefined) {
                groupFound = group;
                return;
            }
        });

        if (typeof success === "function") {
            success(groupFound);
        }

        return groupFound;
    }

    // Events

    function onWindowMoved(callback?: (windowId: string, oldGroup: Glue42.Windows.Group, newGroupId: string) => void): UnsubscribeFunction {
        return _registry.add("window-moved", callback);
    }

    // Private

    function createOrGetGroup(groupId: string): GroupWrapper {
        if (!_groups.hasOwnProperty(groupId)) {
            const createdGroup: GroupWrapper = groupFactory(groupId, environment.executor);
            _groups[groupId] = createdGroup;
            return createdGroup;
        } else {
            return _groups[groupId];
        }
    }

    function deleteGroup(groupId: string): void {
        if (_groups.hasOwnProperty(groupId) && typeof _groups[groupId] !== "undefined") {
            if (_groups[groupId].groupAPI.windows.length === 0) {
                delete _groups[groupId];
            }
        }
    }

    function addNewWindow(win: WindowWrapper) {
        _logger.trace(`Adding new window ${win.API.id} to win.API.groupId ${win.API.groupId}`);
        const group = addWindow(win);
        if (group) {
            _logger.trace(`handleGroupAssociation ${win.API.id} to group.groupAPI.id ${group.groupAPI.id}`);
            win.Events.handleGroupAssociation(group.groupAPI);
        }
    }

    function addWindow(win: WindowWrapper, groupId?: string): GroupWrapper {
        const windowGroupId: string = groupId || win.API.groupId;
        const windowId: string = win.API.id;
        if (typeof windowGroupId === "undefined" || typeof windowId === "undefined") {
            _logger.debug(`trying to add a window without group - winId: ${windowId}`);
            return;
        }

        const group = createOrGetGroup(windowGroupId);
        group.groupInternal.addWindow(windowId);
        return group;
    }

    function removeWindow(win: WindowWrapper, groupId?: string) {
        const windowId: string = win.API.id;
        const windowGroupId: string = groupId || win.API.groupId;
        if (typeof windowGroupId !== "undefined") {
            const group = _groups[windowGroupId];
            group.groupInternal.removeWindow(windowId);
            win.Events.handleGroupAssociation(undefined);
            deleteGroup(windowGroupId);
        }
    }

    function handleCompositionChanged(windowInfo: any): void {
        const newGroupId: string = windowInfo.groupId;
        const windowId: string = windowInfo.windowId;
        const oldGroup: Glue42.Windows.Group = findGroupByWindow(windowId);
        const oldGroupId = oldGroup ? oldGroup.id : undefined;
        _logger.trace(`handleCompositionChanged newGroupId: ${newGroupId} windowId: ${windowId} oldGroup: ${oldGroupId}`);
        if (oldGroupId === newGroupId) {
            _logger.trace(`handleCompositionChanged newGroupId: ${newGroupId} windowId: ${windowId} oldGroup: ${oldGroupId} are the same - returning...`);
            return;
        }

        const win = windowStore.get(windowId) || windowStore.get(windowId);
        const newGroup = addWindow(win, newGroupId);
        if (oldGroup) {
            removeWindow(win, oldGroupId);
            _registry.execute("window-moved", windowId, oldGroup, newGroupId);
        }
        win.Events.handleGroupChanged(newGroup.groupAPI, oldGroup);
    }

    //#region "API"

    const groups = {
        get my() {
            return my();
        },
        list,
        findGroupByWindow,
    };

    //#endregion "API

    const events: GroupsInternal = { onWindowMoved };

    return {
        groupsAPI: groups,
        groupsEvents: events,
    };
};
