    // Calls the public methods of the window. No Call to GD is made
    public async setStyle(
        resultWindow: Glue42.Windows.GDWindow,
        style: Glue42.Windows.WindowSettings
    ): Promise<Glue42.Windows.GDWindow> {
        const stylePromises: Array<Promise<any>> = [];
        const wait = (promise: Promise<any>) => stylePromises.push(promise);

        if (style.focus !== undefined && !resultWindow.isFocused) {
            wait(resultWindow.focus());
        }

        // Todo: only if !isRestoring (hopefully setVisible does this check)
        if (style.hidden !== undefined) {
            const toBeVisible = !style.hidden;
            wait(resultWindow.setVisible(toBeVisible));
        }

        if (style.onTop !== undefined) {
            wait(resultWindow.setOnTop(style.onTop));
        }

        if (style.tabTooltip !== undefined || (style as any).tabToolTip !== undefined) {
            const toolTip = style.tabTooltip ?? (style as any).tabToolTip;
            wait(resultWindow.setTabTooltip(toolTip));
        }

        const {
            maxWidth, maxHeight, minWidth, minHeight,
            allowClose, allowCollapse, allowLockUnlock, allowMaximize, allowMinimize
        } = style;

        wait(resultWindow.setSizeConstraints({ maxWidth, maxHeight, minWidth, minHeight }));
        wait(resultWindow.resetButtons({ allowClose, allowCollapse, allowLockUnlock, allowMaximize, allowMinimize }));

        await Promise.all(stylePromises);

        return resultWindow;
    }

