    public snap(
        resultWindow: Glue42.Windows.GDWindow,
        target: string | Glue42.Windows.GDWindow,
        direction: Glue42.Windows.RelativeDirection
    ): Promise<Glue42.Windows.GDWindow> {
        return new Promise((resolve, reject) => {
            const sourceWindowId = resultWindow.id;
            let targetWindowId;
            const errorMessage =
                "Invalid target parameter - should be an object with property id or a string. Invoked for source window id:" +
                resultWindow.id;
            if (!target) {
                reject(errorMessage);
                return;
            }
            if (typeof target === "string") {
                targetWindowId = target;
            } else if (typeof target.id !== "undefined") {
                targetWindowId = target.id;
            } else {
                reject(errorMessage);
                return;
            }
            const args: any = {
                targetWindowId
            };

            if (direction) {
                args.snappingEdge = direction;
            }
            this.execute("snap", {
                windowId: resultWindow.id,
                options: args
            })
                .then(() => {
                    resolve(resultWindow);
                })
                .catch((e: string) => {
                    reject(e);
                });
        });
    }

