    public execute(
        command: string,
        options: any
    ): Promise<any> {
        return new Promise((resolve, reject) => {
            const args: any = { ...options, command };
            this.agm
                .invoke("T42.Wnd.Execute", args, this.agmTarget)
                .then((i: Glue42Core.AGM.InvocationResult<any>) => {
                    if (i.returned && i.returned.errorMsg) {
                        reject(i);
                    } else {
                        resolve(i.returned);
                    }
                })
                .catch((i: Glue42Core.AGM.InvocationResult<any>) => {
                    reject(i);
                });
        });
    }

