import { default as windowStore } from "../store";
import { WindowWrapper } from "../types";

export function getWindowsByTabGroupId(windowId: string, tabGroupId: string) {
    const windows = windowStore.list;
    return Object.keys(windows).reduce((memo: { [key: string]: WindowWrapper }, id) => {
        const win = windows[id];
        if (win.API.tabGroupId === tabGroupId && win.API.id !== windowId) {
            memo[id] = win;
        }
        return memo;
    }, {});
}
