    function setTabHeaderVisible(toBeTabHeaderVisible: boolean, success?: (window: Glue42.Windows.GDWindow) => void, error?: (error: string) => void): Promise<Glue42.Windows.GDWindow> {
        return new Promise((resolve, reject) => {
            executor.setTabHeaderVisible(resultWindow, toBeTabHeaderVisible)
                .then(() => {
                    if (_isTabHeaderVisible === toBeTabHeaderVisible) {
                        if (typeof success === "function") {
                            success(resultWindow);
                        }

                        return resolve(resultWindow);
                    }
                    const un = onTabHeaderVisibilityChanged((win: Glue42.Windows.GDWindow) => {
                        if (win.id === id && win.isTabHeaderVisible === toBeTabHeaderVisible) {
                            if (typeof success === "function") {
                                success(resultWindow);
                            }
                            un();
                            resolve(resultWindow);
                        }
                    });
                })
                .catch((e: string) => {
                    if (typeof error === "function") {
                        error(e);
                    }
                    reject(e);
                });
        });
    }

