    // This is used to set the relation from window to corespondent group API, it could be undefined, if the window is removed from group
    function handleGroupAssociation(group: Glue42.Windows.Group) {
        if (group) {
            _logger.trace(`setting group to win: ${id} with group id: ${group.id}`);
        }
        _group = group;
    }

    //#region "Internal function"

    function getAllTabs(): Glue42.Windows.GDWindow[] {
        const allWindows = windowStore.list;
        if (_mode.toLowerCase() !== "tab") {
            return [];
        }
        return Object.keys(allWindows).reduce((memo, win) => {
            const window: WindowWrapper = allWindows[win];
            if (window
                && window.API.tabGroupId
                && typeof window.API.tabGroupId !== "undefined"
                && typeof resultWindow.tabGroupId !== "undefined"
                && window.API.tabGroupId === resultWindow.tabGroupId) {
                memo.push(window.API);
            }
            return memo;
        }, []);
    }

    function mapWindowIdsToWindowObjects(windowIdArr: string[]): Glue42.Windows.GDWindow[] {
        return windowIdArr.reduce((memo, winId) => {
            const window = windowStore.get(winId);
            if (window) {
                memo.push(window.API);
            }
            return memo;
        }, []);
    }

    function getNeighboursByDirection(direction: string): Glue42.Windows.GDWindow[] {
        const windowIds = _neighbours[direction];
        if (typeof windowIds !== "undefined") {
            return mapWindowIdsToWindowObjects(windowIds);
        }
    }

    function getApplicationName(): string {
        if (_context._APPLICATION_NAME) {
            return _context._APPLICATION_NAME;
        }
        if (_context && _context._t42 && _context._t42.application) {
            return _context._t42.application;
        }
        const info = getWindowInfo();
        if (!info) {
            return undefined;
        }

        return info.applicationName;
    }

    function getWindowInfo() {
        if (typeof window.glue42gd !== "undefined" && window.glue42gd.getWindowInfo) {
            const info = window.glue42gd.getWindowInfo(id);
            if (!info) {
                return undefined;
            } else {
                return info;
            }
        }
    }

    function areBoundsEqual(requested: Partial<Glue42.Windows.Bounds>, current: Glue42.Windows.Bounds) {
        let height = requested.height;
        let width = requested.width;
        if (requested.height < _settings.minHeight) {
            height = _settings.minHeight;
        }
        if (requested.height > _settings.maxHeight) {
            height = _settings.maxHeight;
        }
        if (requested.width < _settings.minWidth) {
            width = _settings.minWidth;
        }
        if (requested.width > _settings.maxWidth) {
            width = _settings.maxWidth;
        }

        const areHeightsEqual = height ? current.height === height : true;
        const areWidthsEqual = width ? current.width === width : true;
        const areLeftsEqual = requested.left ? current.left === requested.left : true;
        const areTopsEqual = requested.top ? current.top === requested.top : true;

        return areHeightsEqual && areWidthsEqual && areLeftsEqual && areTopsEqual;
    }

