import callbackRegistry, { CallbackRegistry } from "callback-registry";
import { Glue42Core } from "@glue42/core";
import { Glue42 } from "../../glue";
import { default as windowStore } from "./store";
import { Executor, GroupWrapper, WindowWrapper } from "./types";

export default (id: string, executor: Executor): GroupWrapper => {

    const _registry: CallbackRegistry = callbackRegistry();

    const _windowsId: string[] = [];

    //#region "API functions"

    function addWindow(winId: string): void {
        if (_windowsId.indexOf(winId) === -1) {
            _windowsId.push(winId);
            const window: WindowWrapper = windowStore.get(winId);
            window.Events.handleGroupAssociation(groupObject);
            _registry.execute("window-added", groupObject, window.API);
        }
    }

    function removeWindow(winId: string): void {
        const index: number = _windowsId.indexOf(winId);
        if (index !== -1) {
            _windowsId.splice(index, 1);
            const window: Glue42.Windows.GDWindow = _mapToWindowObject(winId);
            _registry.execute("window-removed", groupObject, window);
        }
    }

    function find(window: string | Glue42.Windows.GDWindow, success?: (window: Glue42.Windows.GDWindow) => void): Glue42.Windows.GDWindow {
        let winId: string;
        if (typeof window === "string") {
            winId = window;
        } else {
            winId = window.id;
        }
        const index: number = _windowsId.indexOf(winId);
        if (index !== -1) {
            const mappedWindow: Glue42.Windows.GDWindow = _mapToWindowObject(_windowsId[index]);
            if (typeof success === "function") {
                success(mappedWindow);
            }
            return mappedWindow;
        }
    }

    function windows(success?: (windows: Glue42.Windows.GDWindow[]) => void): Glue42.Windows.GDWindow[] {
        const mappedWindows: Glue42.Windows.GDWindow[] = _mapToWindowObjects();
        if (typeof success === "function") {
            success(mappedWindows);
        }
        return mappedWindows;
    }

    function showHeader(success?: (group: Glue42.Windows.Group) => void, error?: Glue42Core.AGM.InvokeErrorHandler): Promise<Glue42.Windows.Group> {
        return new Promise((resolve, reject) => {
            Promise.all([
                executor.setGroupHeaderVisible(_windowsId[0], true),
                waitForHeaderVisibilityChanged()
            ])
                .then(() => {
                    if (typeof success === "function") {
                        success(groupObject);
                    }
                    resolve(groupObject);
                })
                .catch((e: any) => {
                    reject(e);
                });
        });
    }

    function hideHeader(success?: (group: Glue42.Windows.Group) => void, error?: () => void): Promise<Glue42.Windows.Group> {
        return new Promise((resolve, reject) => {
            Promise.all([
                executor.setGroupHeaderVisible(_windowsId[0], false),
                waitForHeaderVisibilityChanged()
            ])
                .then(() => {
                    if (typeof success === "function") {
                        success(groupObject);
                    }
                    resolve(groupObject);
                })
                .catch((e: any) => {
                    return;
                });
        });
    }

    function getTitle(): Promise<string> {
        return executor.getGroupTitle(_windowsId[0]);
    }

    function setTitle(title: string): Promise<void> {
        return executor.setGroupTitle(_windowsId[0], title);
    }

    function capture(captureOptions: Glue42.Windows.CaptureOptions): Promise<string> {
        return executor.captureGroup(_windowsId, captureOptions);
    }

    function waitForHeaderVisibilityChanged(): Promise<void> {
        return new Promise((resolve, reject) => {
            const unsub = onHeaderVisibilityChanged((group) => {
                unsub();
                resolve();
            });

            // setTimeout(() => {
            //     resolve();
            // }, 1500);
        });
    }

    function execute(command: string, options?: object, success?: (group: Glue42.Windows.Group) => void, error?: (err?: any) => void): Promise<Glue42.Windows.Group> {
        return new Promise((resolve, reject) => {
            executor.execute<Glue42.Windows.Group>(command, options)
                .then((w) => {
                    if (typeof success === "function") {
                        success(groupObject);
                    }
                    resolve(groupObject);
                })
                .catch((e: any) => {
                    reject(e);
                });
        });
    }

    //#endregion "API functions"

    //#region "Stream function"

    function handleGroupHeaderVisibilityChanged(windowInfo: any): void {
        _registry.execute("header-visibility-changed", groupObject);
    }

    //#endregion "Stream function"

    //#region "Internal function"

    function _mapToWindowObjects(): Glue42.Windows.GDWindow[] {
        const winObjects: Glue42.Windows.GDWindow[] = [];

        _windowsId.forEach((winId) => {
            const windowObject = _mapToWindowObject(winId);
            if (typeof windowObject !== "undefined") {
                winObjects.push(windowObject);
            }
        });

        return winObjects;
    }

    function _mapToWindowObject(windowId: string): Glue42.Windows.GDWindow {
        return windowStore.get(windowId) ? windowStore.get(windowId).API : undefined;
    }

    function _getGroupHeaderVisibility(): boolean {
        // if one of the windows has a flag hideGroupHeader the group header should be off.
        const windowWithHiddenHeader = _mapToWindowObjects().find((w) => !w.isGroupHeaderVisible);
        const _isGroupHeaderVisible = windowWithHiddenHeader === undefined;

        return _isGroupHeaderVisible;
    }

    //#endregion "Internal function"

    //#region "Events"

    function onHeaderVisibilityChanged(callback?: (group: Glue42.Windows.Group) => void): () => void {
        return _registry.add("header-visibility-changed", callback);
    }

    function onWindowAdded(callback?: (group: Glue42.Windows.Group, window: Glue42.Windows.GDWindow) => void): () => void {
        return _registry.add("window-added", callback);
    }

    function onWindowRemoved(callback?: (group: Glue42.Windows.Group, window: Glue42.Windows.GDWindow) => void): () => void {
        return _registry.add("window-removed", callback);
    }

    //#endregion "Events"

    //#region "API"

    const groupObject: Glue42.Windows.Group = {
        id,
        get windows() {
            return windows();
        },
        find,
        get isHeaderVisible() {
            return _getGroupHeaderVisibility();
        },
        showHeader,
        hideHeader,
        getTitle,
        setTitle,
        capture,
        maximize: (success, error) => {
            return execute("maximize", { groupId: id }, success, error);
        },
        restore: (success, error) => {
            return execute("restore", { groupId: id }, success, error);
        },

        onHeaderVisibilityChanged,
        onWindowAdded,
        onWindowRemoved,
    };

    const internal = {
        addWindow,
        removeWindow,
        handleGroupHeaderVisibilityChanged,
    };

    return {
        groupAPI: groupObject,
        groupInternal: internal,
    };

    //#endregion "API"
};
