import { Glue42 } from "../../glue";
import generate from "shortid";

const T42_INTENTS_COMMAND = "T42.Intents.Command";
const T42_INTENTS_EVENTS = "T42.Intents.Events";
let agm: Glue42.AGM.API;

enum IntentCommand {
    Find = "find",
    FindByContext = "findByContext",
    GetAll = "all",
    Raise = "raise",
}

interface Handler {
    unsubId: string;
    callback: (context: object) => void;
}

const intentListeners: { [intent: string]: Handler[] } = {};

async function raise(request: string | Glue42.Intents.IntentRequest): Promise<Glue42.Intents.IntentResult> {
    if (typeof request === "string") {
        request = { intent: request };
    }

    return invokeGlue<Glue42.Intents.IntentResult>(IntentCommand.Raise, request);
}

async function find(intentFilter?: string | Glue42.Intents.IntentFilter): Promise<Glue42.Intents.Intent> {
    if (typeof intentFilter === "string") {
        intentFilter = { name: intentFilter };
    }

    // TODO: review filters
    return invokeGlue<Glue42.Intents.Intent>(IntentCommand.Find, { intentFilter });
}

async function findByContext(contextType: string): Promise<Glue42.Intents.Intent[]> {
    if (!contextType || typeof contextType !== "string") {
        return Promise.reject(Error(`findByContext called with invalid contextType: ${contextType}`));
    }

    return invokeGlue<Glue42.Intents.Intent[]>(IntentCommand.FindByContext, { contextType });
}

async function all(): Promise<Glue42.Intents.Intent[]> {
    return invokeGlue<Glue42.Intents.Intent[]>(IntentCommand.GetAll);
}

function addIntentListener(intent: string, handler: (context: object) => void): () => void {
    if (!intentListeners[intent]) {
        intentListeners[intent] = [];
    }

    const unsubId: string = generate();
    intentListeners[intent].push({ unsubId, callback: handler });

    return () => {
        intentListeners[intent] = intentListeners[intent]
            .filter((h) => h.unsubId !== unsubId);
    };
}

async function invokeGlue<T>(command: string, options?: object): Promise<T> {
    // Consider targetting only GD
    const invocationResult = await agm.invoke(T42_INTENTS_COMMAND, { options: { ...options }, command });
    return invocationResult.returned.result;
}

export function factory(agmAPI: Glue42.AGM.API): Glue42.Intents.API & { ready: () => Promise<void>; } {
    agm = agmAPI;

    agm.register(T42_INTENTS_EVENTS, handleEvent);

    return {
        raise,
        find,
        findByContext,
        all,
        addIntentListener,
        ready: () => Promise.resolve()
    };
}

enum IntentEvents {
    IntentListenerRaised = "intentListenerRaised"
}

interface IntentEventData {
    type: IntentEvents;
    intent: string;

    [prop: string]: any;
}

function handleEvent(args: IntentEventData) {
    const { type, intent, context } = args;

    switch (type) {
        case IntentEvents.IntentListenerRaised:
            const callbacks = intentListeners[intent]?.map((handler) => handler.callback);
            // Todo: What should happen when there is no context ?
            if (callbacks && callbacks.length > 0) {
                callbacks.forEach((cb) => cb(context));
            }

    }
}
